	module.exports = {"data":{"markdownRemark":{"html":"<p>Hầy, tôi viết blog này như kiểu tự thoại nội tâm vậy. Vì việc học code khó quá mà tiền thì ko có nhiều nên đành phải cố tự mình dạy cho mình học thôi. Tôi viết phần nhiều bằng Tiếng Anh vì ko muốn trình Tiếng Anh của mình bị hao mòn, nhưng hôm nay lại tự nhiên sợ nếu mình kém Tiếng Việt thì sao. Sau này đi làm chắc quái gì đã suốt ngày gặp khách hàng nước ngoài mà toàn khách Việt thì sao, mà cả đồng nghiệp cũng toàn người Việt. Nếu TV ko khá thì sao hiểu nổi yêu cầu của khách, sao có thể học được từ người giỏi hơn và dạy được cho người kém khá hơn.</p>\n<p><strong>Thế nên hôm nay Hưng sẽ viết 1 bài TA ra kèm sau là 1 bài Tiếng Việt.</strong></p>\n<p>Hơi bị khập khiễng thật, cơ mà đối tượng chính cũng chỉ có mình tôi thôi nên kệ nó đi! Chắc lâu nữa khi đọc lại vẫn hiểu.</p>\n<p>Thôi lập trình vui vẻ nhé Hưng!</p>","frontmatter":{"date":"March 27, 2018","path":"/quy-tac","title":"Quy tắc"}}},"pathContext":{}}

